<template>
    <div>
        <div
                class="modal fade"
                id="edit-my-poll-popup"
                tabindex="-1"
                role="dialog"
                aria-labelledby="edit-my-poll-popup"
                aria-hidden="true"
        >
            <div class="modal-dialog window-popup edit-my-poll-popup" role="document">
                <div class="modal-content">
                    <a href="#" class="close icon-close" data-dismiss="modal" aria-label="Close">
                        <svg class="olymp-close-icon">
                            <use xlink:href="svg-icons/sprites/icons.svg#olymp-close-icon" />
                        </svg>
                    </a>
                    <div class="modal-body" ref="showMatchReviewBox">
                        <!-- <div class="control-block-button post-control-button">
                          <a href="#" class="btn btn-control has-i bg-facebook">
                            <i class="fab fa-facebook-f" aria-hidden="true"></i>
                          </a>

                          <a href="#" class="btn btn-control has-i bg-twitter">
                            <i class="fab fa-twitter" aria-hidden="true"></i>
                          </a>
                        </div>-->

                        <div class="edit-my-poll-head bg-mdd">
                            <div class="head-content">
                                <h2 class="title">Give Your Review</h2>
                                <div class="place inline-items">
                                    <svg class="olymp-add-a-place-icon">
                                        <use xlink:href="svg-icons/sprites/icons.svg#olymp-add-a-place-icon" />
                                    </svg>
                                    <span>{{ homeTeamName }}</span>
                                    <span>{{ awayTeamName }}</span>
                                </div>
                            </div>

                            <div class="sidebyside">
                                <img class="poll-img" :src="home_team_logo" :alt="homeTeamName" />
                                <h4>VS</h4>
                                <img class="poll-img" :src="away_team_logo" :alt="awayTeamName" />
                            </div>
                        </div>

                        <div class="edit-my-poll-content">
                            <response-message :data="errorData" />
                            <form method="post" enctype="multipart/form-data" class="resume-form">
                                <h3>Submit Your Review</h3>
                                <div class="row">
                                    <div class="col col-12 col-xl-6 col-lg-6 col-md-6 col-sm-12">
                                        <div @click="getImage()" class="form-group with-button">
                                            <input
                                                    class="form-control"
                                                    id="imageName"
                                                    placeholder="Browse Image..."
                                                    value
                                                    type="text"
                                            />
                                            <button type="button" @click="getImage()" class="bg-grey">
                                                <svg class="olymp-computer-icon">
                                                    <use xlink:href="/svg-icons/sprites/icons.svg#olymp-computer-icon" />
                                                </svg>
                                            </button>
                                        </div>
                                        <input
                                                type="file"
                                                @change="previewImage"
                                                style="display:none"
                                                id="getImage"
                                                name="image_name"
                                        />
                                        <canvas style="display:none" id="canvas"></canvas>
                                    </div>
                                    <div class="col col-12 col-xl-12 col-lg-12 col-md-12 col-sm-12">
                                        <div v-if="justPhoto === false" class="form-group label-floating is-empty">
                                            <label class="control-label">Your Review</label>
                                            <textarea
                                                    v-model="formModelReview.review"
                                                    name="review"
                                                    class="form-control"
                                                    placeholder
                                                    id="review"
                                            ></textarea>
                                        </div>
                                        <button
                                                id="reviewPostButton"
                                                @click.prevent="uploadReview"
                                                class="btn bg-mdd btn-lg full-width"
                                        >Submit Review</button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "PostReview.vue",
        data() {
            return {
                homeTeamName: "",
                awayTeamName: "",
                home_team_logo: "",
                away_team_logo: "",
                setSearchResults: "",
                matchId: "",
                errorData: {},
                imageData: "string",
                imageName: "",
                formModelReview: {
                    review: ""
                },
                justPhoto: false,
            };
        },
        methods: {
            showLoading() {
                let loader = this.$loader(this, this.$refs.showMatchReviewBox, {
                    loaderType: "spinner"
                });
                return loader;
            },
            setMatch() {
                // let loader = this.showLoading();
                this.errorData = {
                    isError: false
                };
                let matchId = this.setSearchResults;
                this.axios
                    .get("fixtures/info/" + matchId)
                    .then(response => {
                        // loader.hide();
                        // console.log(response.data.data);
                        this.matchId = response.data.data.id;
                        this.homeTeamName = response.data.data.home_team_name;
                        this.awayTeamName = response.data.data.away_team_name;
                        this.home_team_logo = response.data.data.home_team_logo;
                        this.away_team_logo = response.data.data.away_team_logo;
                    })
                    .catch(error => {
                        // loader.hide();
                        $("#edit-my-poll-popup").modal("hide");
                        // console.log(error.response);
                    });
            },
            getImage() {
                $("#getImage").click();
            },
            previewImage(ev) {
                if ($("#getImage").get(0).files.length > 0) {
                    if (
                        $("#getImage")
                            .get(0)
                            .files[0].name.split(".")
                            .pop() !== "png" &&
                        $("#getImage")
                            .get(0)
                            .files[0].name.split(".")
                            .pop() !== "jpg" &&
                        $("#getImage")
                            .get(0)
                            .files[0].name.split(".")
                            .pop() !== "jpeg"
                    ) {
                        this.errorData = {
                            message: "Sorry image can only be (Jpg - Png)",
                            isError: true
                        };
                    } else {
                        let file = $("#getImage").get(0).files[0].name;
                        $("#imageName").val(file);
                        this.getBase64();
                    }
                }
            },
            // get and convert any uploaded image to base64 image
            getBase64() {
                // we use this to get the image and handle validation
                let file = $("#getImage").get(0).files[0];
                var reader = new FileReader();
                reader.onloadend = e => {
                    var image = new Image();
                    image.src = e.target.result;
                    image.onload = ev => {
                        let _this = this;
                        // we initialize the image convertions to canvas
                        var canvas = document.getElementById("canvas");
                        canvas.width = image.width;
                        canvas.height = image.height;
                        canvas.getContext("2d").drawImage(image, 0, 0);
                        // once converted to canvas, now convert to png
                        var ctx = new Image();
                        ctx.src = canvas.toDataURL("image/png");
                        //return converted image base64 only6
                        this.imageData = ctx.src;
                    };
                };
                reader.readAsDataURL(file);
            },
            getImageDetails() {
                if ($("#getImage").get(0).files.length > 0) {
                    let file = $("#getImage").get(0).files[0].name;
                    $("#imageName").val(file);
                    var splitImageName = file.split(".");
                    return splitImageName[0];
                }
            },
            clearForm() {
                this.formModelReview.review = "";
                this.matchId = "",
                $("#getImage").val("");
                $("#imageName").val("");
            },
            uploadReview() {
                $("#review").attr("disabled", true);
                $("#reviewPostButton").attr("disabled", true);
                $("#imageName").attr("disabled", true);
                $("#getImage").attr("disabled", true);
                let loader = this.showLoading();
                if (this.getImageDetails() !== undefined && this.justPhoto === true ) {
                    // if there is an image but without review
                    let file = $("#getImage").get(0).files[0];
                    let formData = new FormData();
                    formData.append('image', file);
                    formData.append("fixture_id", this.matchId);

                    this.axios({
                        method: "post",
                        url: "reviews/create",
                        data: formData,
                        config: { headers: { "Content-Type": "multipart/form-data" } }
                    })
                        .then(response => {
                            loader.hide();
                            this.errorData = {
                                message: "Your review has been uploaded successfully",
                                isError: "success"
                            };
                            $("#review").defaultValue = "";
                            $("#review").val("");
                            this.formModelReview.review = "";
                            $("#reviewPostButton").attr("disabled", true);
                            $("#imageName").val("");
                            $("#getImage").val("");
                            window.eventBus.$emit("loadFeeds", true);
                            setTimeout(() => {
                                $("#reviewPostButton").attr("disabled", false);
                                $("#review").attr("disabled", false);
                                $("#imageName").attr("disabled", false);
                                $("#edit-my-poll-popup").modal("hide");
                                $("#getImage").attr("disabled", false);
                                this.errorData = {
                                    message: "",
                                    isError: "false"
                                };
                            }, 500);
                            this.errorData = {
                                message: "",
                                isError: false
                            };
                        })
                        .catch(error => {
                            loader.hide();
                            $("#review").attr("disabled", false);
                            $("#reviewPostButton").attr("disabled", false);
                            $("#imageName").attr("disabled", false);
                            $("#getImage").attr("disabled", false);
                            this.errorData = {
                                message: error.response
                                    ? "Please check your Review and image (Jpg, Png - Only)"
                                    : "Sorry an error occured, check your internet",
                                isError: true
                            };
                        });

                }else if(this.getImageDetails() !== undefined && this.justPhoto === false ) {
                    // if there is an image and there is meant to be a review
                    if(this.formModelReview.review !== "") {
                        let file = $("#getImage").get(0).files[0];
                        let formData = new FormData();
                        formData.append('image', file);
                        formData.append("fixture_id", this.matchId);
                        formData.append("reviews", this.formModelReview.review);

                        this.axios({
                            method: "post",
                            url: "reviews/create",
                            data: formData,
                            config: { headers: { "Content-Type": "multipart/form-data" } }
                        })
                            .then(response => {
                                loader.hide();
                                this.errorData = {
                                    message: "Your review has been uploaded successfully",
                                    isError: "success"
                                };
                                $("#review").defaultValue = "";
                                $("review").val("");
                                $("#reviewPostButton").attr("disabled", true);
                                $("#imageName").val("");
                                $("#getImage").val("");
                                window.eventBus.$emit("loadFeeds", true);
                                setTimeout(() => {
                                    $("#reviewPostButton").attr("disabled", false);
                                    $("#review").attr("disabled", false);
                                    $("#imageName").attr("disabled", false);
                                    $("#edit-my-poll-popup").modal("hide");
                                    $("#getImage").attr("disabled", false);
                                    this.errorData = {
                                        message: "",
                                        isError: false,
                                    };
                                }, 500);
                            })
                            .catch(error => {
                                loader.hide();
                                $("#review").attr("disabled", false);
                                $("#reviewPostButton").attr("disabled", false);
                                $("#imageName").attr("disabled", false);
                                $("#getImage").attr("disabled", false);
                                this.errorData = {
                                    message: error.response
                                        ? "Please check your Review and image (Jpg, Png - Only)"
                                        : "Sorry an error occured, check your internet",
                                    isError: true
                                };
                            });

                    }else {
                        loader.hide();
                        $("#review").attr("disabled", false);
                        $("#reviewPostButton").attr("disabled", false);
                        $("#imageName").attr("disabled", false);
                        $("#getImage").attr("disabled", false);
                        this.errorData = {
                            message: "Review box cannot be empty",
                            isError: true
                        };
                    }
                }else {
                    // else if it's only review
                    // handle the request payload
                    if(this.formModelReview.review !== "") {
                        let request_payload = {
                            fixture_id: this.matchId,
                            reviews: this.formModelReview.review,
                        };
                        this.axios
                            .post("reviews/create", request_payload)
                            .then(response => {
                                loader.hide();
                                // console.log("Showing result");
                                this.errorData = {
                                    message: "Your review has been uploaded successfully",
                                    isError: "success"
                                };
                                $("#review").defaultValue = "";
                                $("#review").val("");
                                $("#reviewPostButton").attr("disabled", true);
                                $("#imageName").val("");
                                $("#getImage").val("");
                                window.eventBus.$emit("loadFeeds", true);
                                setTimeout(() => {
                                    $("#reviewPostButton").attr("disabled", false);
                                    $("#review").attr("disabled", false);
                                    $("#imageName").attr("disabled", false);
                                    $("#edit-my-poll-popup").modal("hide");
                                    $("#getImage").attr("disabled", false);
                                    this.errorData = {
                                        message: "",
                                        isError: "false"
                                    };
                                }, 500);
                            })
                            .catch(error => {
                                loader.hide();
                                $("#review").attr("disabled", false);
                                $("#reviewPostButton").attr("disabled", false);
                                $("#imageName").attr("disabled", false);
                                $("#getImage").attr("disabled", false);
                                this.errorData = {
                                    message: error.response
                                        ? "Please check your Review"
                                        : "Sorry an error occured, check your internet",
                                    isError: true
                                };
                                // console.log(error.response);
                            });
                    }else {
                        loader.hide();
                        $("#review").attr("disabled", false);
                        $("#reviewPostButton").attr("disabled", false);
                        $("#imageName").attr("disabled", false);
                        $("#getImage").attr("disabled", false);
                        this.errorData = {
                            message: "Sorry either give a review or upload an image",
                            isError: true,
                        };
                    }
                }
            }
        },
        async mounted() {
            // set match id if emited id. this is coming from post photos button
            window.eventBus.$on("matchID", val => {
                this.setSearchResults = val;
                this.setMatch();
            });
            window.eventBus.$on("review_option", val => {
               if(val === "photo") {
                   this.justPhoto = true;
               } else if(val === "review") {
                   this.justPhoto = false;
               }
            });

        }
    }
</script>

