<template>
    <div>
        <div
                class="modal fade"
                id="edit-my-poll-update"
                tabindex="-1"
                role="dialog"
                aria-labelledby="edit-my-poll-update"
                aria-hidden="true"
        >
            <div class="modal-dialog window-popup edit-my-poll-popup" role="document">
                <div class="modal-content">
                    <a href="#" class="close icon-close" data-dismiss="modal" aria-label="Close">
                        <svg class="olymp-close-icon">
                            <use xlink:href="svg-icons/sprites/icons.svg#olymp-close-icon" />
                        </svg>
                    </a>
                    <div class="modal-body" ref="showMatchReviewBox">
                        <!-- <div class="control-block-button post-control-button">
                          <a href="#" class="btn btn-control has-i bg-facebook">
                            <i class="fab fa-facebook-f" aria-hidden="true"></i>
                          </a>

                          <a href="#" class="btn btn-control has-i bg-twitter">
                            <i class="fab fa-twitter" aria-hidden="true"></i>
                          </a>
                        </div>-->

                        <div class="edit-my-poll-head bg-mdd">
                            <div class="head-content">
                                <h2 class="title">Update Your Review</h2>
                                <div class="place inline-items">
                                    <svg class="olymp-add-a-place-icon">
                                        <use xlink:href="svg-icons/sprites/icons.svg#olymp-add-a-place-icon" />
                                    </svg>
                                    <span>{{ homeTeamName }}</span>
                                    <span>{{ awayTeamName }}</span>
                                </div>
                            </div>

                            <div class="sidebyside">
                                <img class="poll-img" :src="home_team_logo" :alt="homeTeamName" />
                                <h4>VS</h4>
                                <img class="poll-img" :src="away_team_logo" :alt="awayTeamName" />
                            </div>
                        </div>

                        <div class="edit-my-poll-content">
                            <response-message :data="errorData" />
                            <form method="post" enctype="multipart/form-data" class="resume-form">
                                <h3>Submit Your Review</h3>
                                <div class="row">
                                    <div class="col col-12 col-xl-6 col-lg-6 col-md-6 col-sm-12">
                                        <div @click="getImage()" class="form-group with-button">
                                            <input
                                                    class="form-control"
                                                    id="imageNName"
                                                    placeholder="Browse Image..."
                                                    value
                                                    type="text"
                                            />
                                            <button type="button" @click="getImage()" class="bg-grey">
                                                <svg class="olymp-computer-icon">
                                                    <use xlink:href="/svg-icons/sprites/icons.svg#olymp-computer-icon" />
                                                </svg>
                                            </button>
                                        </div>
                                        <input
                                                type="file"
                                                @change="previewImage"
                                                style="display:none"
                                                id="gettImage"
                                                name="image_name"
                                        />

                                    </div>
                                    <div class="col col-12 col-xl-6 col-lg-6 col-md-6 col-sm-12">
                                        <div class="preview-image"></div>
                                    </div>
                                    <div class="col col-12 col-xl-12 col-lg-12 col-md-12 col-sm-12">
                                        <div v-if="justPhoto === false" class="form-group label-floating">
                                            <label class="control-label">Your Review</label>
                                            <textarea
                                                    v-model="formModelReview.review"
                                                    name="review"
                                                    class="form-control review"
                                                    placeholder
                                                    id="review"
                                            ></textarea>
                                        </div>
                                        <button
                                                id="rreviewPostButton"
                                                @click.prevent="uploadReview"
                                                class="btn bg-mdd btn-lg full-width"
                                        >Submit Review</button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "updateReview",
        data() {
            return {
                homeTeamName: "",
                awayTeamName: "",
                home_team_logo: "",
                away_team_logo: "",
                setSearchResults: "",
                matchId: "",
                errorData: {},
                imageData: "string",
                imageName: "",
                formModelReview: {
                    review: ""
                },
                justPhoto: false,
            };
        },
        methods: {
            showLoading() {
                let loader = this.$loader(this, this.$refs.showMatchReviewBox, {
                    loaderType: "spinner"
                });
                return loader;
            },
            setMatch(val) {
                console.log(val);
                this.homeTeamName = val.fixture.home_team_name;
                this.awayTeamName = val.fixture.away_team_name;
                this.home_team_logo = val.fixture.home_team_logo;
                this.away_team_logo = val.fixture.away_team_logo;
                this.formModelReview.review = val.review;
                // $('#imageName').html(val.images[0].src);
                $('.preview-image').html(`<img class="preview-img" src='${val.images[0].src}'>`)
            },
            getImage() {
                $("#gettImage").click();
            },
            previewImage(ev) {
                if ($("#gettImage").get(0).files.length > 0) {
                    if (
                        $("#gettImage")
                            .get(0)
                            .files[0].name.split(".")
                            .pop() !== "png" &&
                        $("#gettImage")
                            .get(0)
                            .files[0].name.split(".")
                            .pop() !== "jpg" &&
                        $("#gettImage")
                            .get(0)
                            .files[0].name.split(".")
                            .pop() !== "jpeg"
                    ) {
                        this.errorData = {
                            message: "Sorry image can only be (Jpg - Png)",
                            isError: true
                        };
                    } else {
                        let file = $("#gettImage").get(0).files[0].name;
                        $("#imageNName").val(file);
                        console.log(file)
                        // this.getBase64();
                    }
                }
            },

            getImageDetails() {
                if ($("#gettImage").get(0).files.length > 0) {
                    let file = $("#gettImage").get(0).files[0].name;
                    // $("#gettImage").val(file);
                    var splitImageName = file.split(".");
                    return splitImageName[0];
                }
            },
            clearForm() {
                this.formModelReview.review = "";
                this.matchId = "",
                    $("#gettImage").val("");
                $("#imageNName").val("");
            },
            uploadReview() {
                $("#review").attr("disabled", true);
                $("#reviewPostButton").attr("disabled", true);
                $("#imageNName").attr("disabled", true);
                $("#gettImage").attr("disabled", true);
                let loader = this.showLoading();
                if(this.getImageDetails() !== undefined ) {
                    // if there is an image and there is meant to be a review
                    if(this.formModelReview.review !== "") {
                        let file = $("#gettImage").get(0).files[0];
                        let formData = new FormData();
                        formData.append('image', file);
                        formData.append("reviews", this.formModelReview.review);
                        formData.append("review_id", this.setSearchResults);

                        this.axios({
                            method: "post",
                            url: "reviews/update",
                            data: formData,
                            config: { headers: { "Content-Type": "multipart/form-data" } }
                        })
                            .then(response => {
                                loader.hide();
                                console.log(response)
                                this.errorData = {
                                    message: "Your review has been updated successfully",
                                    isError: "success"
                                };
                                $(".review").defaultValue = "";
                                $(".review").val("");
                                $("#reviewPostButton").attr("disabled", true);
                                $("#imageNName").val("");
                                $("#gettImage").val("");
                                window.eventBus.$emit("loadFeeds", true);
                                this.formModelReview.review = "";
                                setTimeout(() => {
                                    $("#rreviewPostButton").attr("disabled", false);
                                    $(".review").attr("disabled", false);
                                    $("#imageNName").attr("disabled", false);
                                    $("#edit-my-poll-update").modal("hide");
                                    $("#gettImage").attr("disabled", false);
                                    this.errorData = {
                                        message: "",
                                        isError: false,
                                    };
                                }, 500);
                            })
                            .catch(error => {
                                loader.hide();
                                $(".review").attr("disabled", false);
                                $("#rreviewPostButton").attr("disabled", false);
                                $("#imageNName").attr("disabled", false);
                                $("#gettImage").attr("disabled", false);
                                this.errorData = {
                                    message: error.response
                                        ? "Please check your Review and image (Jpg, Png - Only)"
                                        : "Sorry an error occured, check your internet",
                                    isError: true
                                };
                            });

                    }else {
                        loader.hide();
                        $(".review").attr("disabled", false);
                        $("#rreviewPostButton").attr("disabled", false);
                        $("#imageNName").attr("disabled", false);
                        $("#gettImage").attr("disabled", false);
                        this.errorData = {
                            message: "Review box cannot be empty",
                            isError: true
                        };
                    }
                }else {
                    // else if it's only review
                    // handle the request payload
                    if(this.formModelReview.review !== "") {
                        let request_payload = {
                            reviews: this.formModelReview.review,
                            review_id: this.setSearchResults,
                        };
                        this.axios
                            .post("reviews/update/", request_payload)
                            .then(response => {
                                loader.hide();
                                console.log(response);
                                this.errorData = {
                                    message: "Your review has been updated successfully",
                                    isError: "success"
                                };
                                $(".review").defaultValue = "";
                                $(".review").val("");
                                $("#reviewPostButton").attr("disabled", true);
                                $("#imageNName").val("");
                                $("#gettImage").val("");
                                this.formModelReview.review = "";
                                window.eventBus.$emit("loadFeeds", true);
                                setTimeout(() => {
                                    $("#rreviewPostButton").attr("disabled", false);
                                    $(".review").attr("disabled", false);
                                    $("#imageNName").attr("disabled", false);
                                    $("#edit-my-poll-update").modal("hide");
                                    $("#gettImage").attr("disabled", false);
                                    this.errorData = {
                                        message: "",
                                        isError: false,
                                    };
                                }, 500);
                                return response;
                            })
                            .catch(error => {
                                loader.hide();
                                $(".review").attr("disabled", false);
                                $("#rreviewPostButton").attr("disabled", false);
                                $("#imageNName").attr("disabled", false);
                                $("#gettImage").attr("disabled", false);
                                this.errorData = {
                                    message: error.response
                                        ? "Please check your Review"
                                        : "Sorry an error occured, check your internet",
                                    isError: true
                                };
                                // console.log(error.response);
                            });
                    }else {
                        loader.hide();
                        $(".review").attr("disabled", false);
                        $("#rreviewPostButton").attr("disabled", false);
                        $("#imageNName").attr("disabled", false);
                        $("#gettImage").attr("disabled", false);
                        this.errorData = {
                            message: "Sorry either give a review or upload an image",
                            isError: true,
                        };
                    }
                }
            }
        },
        async mounted() {
            // set match id if emited id. this is coming from post photos button
            window.eventBus.$on("updateReview", val => {
                this.setSearchResults = val.review_id;
                this.setMatch(val.feed);
            });
            window.eventBus.$on("review_option", val => {
                if(val === "photo") {
                    this.justPhoto = true;
                } else if(val === "review") {
                    this.justPhoto = false;
                }
            });

        }
    }
</script>

<style scoped>
.preview-image {
    width: 260px;
    height: 200px;
    margin-top: -121px;
    text-align: center;
}
</style>
